'use strict';
var $ = require("jquery");
var ImplementationTypeHelper = require('../../../helper/ImplementationTypeHelper'),
    cmdHelper = require('../../../helper/CmdHelper'),
    elementHelper = require('../../../helper/ElementHelper'),
    InputOutputHelper = require('../../../helper/InputOutputHelper');
var assign = require('lodash/assign');
var utils = require('../../../Utils'),
    escapeHTML = utils.escapeHTML,
    triggerClickEvent = utils.triggerClickEvent;
var notification = null;
var implementationType = require('./implementation/ImplementationType'),
    delegate = require('./implementation/Delegate'),
    external = require('./implementation/External'),
    callable = require('./implementation/Callable'),
    MAIL_NOTIFICATION_SERVICE = require('../../../../constants').MAIL_NOTIFICATION_SERVICE,
    resultVariable = require('./implementation/ResultVariable');
var entryFactory = require('../../../factory/EntryFactory');
const {getExtensionElements} = require("../../../helper/ExtensionElementsHelper");
//const {getExtensionElements} = require("./ExtensionElementsHelper");
var CAMUNDA_FIELD_EXTENSION_ELEMENT = 'camunda:Field';
var domQuery = require('min-dom').query,
    domClosest = require('min-dom').closest,
    domClasses = require('min-dom').classes;

function getImplementationType(element) {
  return ImplementationTypeHelper.getImplementationType(element);
}

function getBusinessObject(element) {

  return ImplementationTypeHelper.getServiceTaskLikeBusinessObject(element);
}

function isDmnCapable(element) {
  return ImplementationTypeHelper.isDmnCapable(element);
}

function isExternalCapable(element) {
  return ImplementationTypeHelper.isExternalCapable(element);
}

function isServiceTaskLike(element) {
  return ImplementationTypeHelper.isServiceTaskLike(element);
}

module.exports = function(group, element, bpmnFactory, translate) {

    if (!isServiceTaskLike(getBusinessObject(element))) {

        return;
    }

    var hasDmnSupport = isDmnCapable(element);
    var hasExternalSupport = isExternalCapable(getBusinessObject(element));

    if(element.type === "bpmn:SendTask") {
    var notificationListSelectEntry = entryFactory.selectBox(translate, {
        id: 'selectNotification',
        label: translate('Select Notification'),
        selectOptions: function (element, node) {
            var arrValues = []

            $.ajax({
                url: MAIL_NOTIFICATION_SERVICE + "/api/notification/auth/getAll/" + JSON.parse(window.localStorage.getItem("domain_data")).domainId,
                // url: "http://41.230.29.238:8081/ISO-1-0.0.1-SNAPSHOT/api/auth/getRoles",
                method: "GET",
                headers: {
                    "X-Tenant": JSON.parse(localStorage.getItem('domain_data')).domainName
                },
                success: function (result) {
                    var tab = [];
                    tab.push({name: 'Please select...', value: ''});
                    //var forms = result;
                    result.map(data =>
                        tab.push({name: data.name + "-" + data.version, value: data.notificationId})
                    );
                    arrValues = tab;
                },
                async: false
            });
            return arrValues;
        },
        //    defaultValue: notification,
        setControlValue: true,
        modelProperty: 'selectNotification',
        set: function (element, values, node) {
            var commands = [];

            var bo = getBusinessObject(element);
            notification = values.selectNotification;

            var props = assign({});
            props.string = "test";
            props.name = "test_name";

            commands.push(cmdHelper.updateProperties(element, {
                'camunda:class': "com.isctgroup.delegates.SendTaskImpl"
            }));
            /*    commands.push(cmdHelper.updateProperties(element, {
                         'camunda:resultVariable':values.selectNotification
                 }));*/
            var extensionElements = elementHelper.createElement('bpmn:ExtensionElements', {values: []}, bo, bpmnFactory);
            commands.push(cmdHelper.updateProperties(element, {extensionElements: extensionElements}));
            $.ajax({
                url: MAIL_NOTIFICATION_SERVICE + "/api/notification/auth/getByNotificationId/" + notification,
                // url: "http://41.230.29.238:8081/ISO-1-0.0.1-SNAPSHOT/api/auth/getRoles",
                method: "GET",
                headers: {
                    "X-Tenant": JSON.parse(localStorage.getItem('domain_data')).domainName
                },
                success: function (result) {
                    //var notification_data = JSON.parse(result);
                    var notification_data = JSON.parse(result.notification);
                    var notification_msg = result.notification_msg;

                    /*    var extensionElements = elementHelper.createElement('bpmn:ExtensionElements', { values: [] }, bo, bpmnFactory);
                              commands.push(cmdHelper.updateProperties(element, { extensionElements: extensionElements }));*/

                    var newFieldElem = elementHelper.createElement(CAMUNDA_FIELD_EXTENSION_ELEMENT, {
                        name: 'notificationId',
                        string: values.selectNotification
                    }, extensionElements, bpmnFactory);
                    commands.push(cmdHelper.addElementsTolist(element, extensionElements, 'values', [newFieldElem]));
                    if (notification_data.notificationTo !== '' && notification_data.notificationTo !== null) {
                        var newFieldElem = elementHelper.createElement(CAMUNDA_FIELD_EXTENSION_ELEMENT, {
                            name: 'to',
                            string: notification_data.notificationTo
                        }, extensionElements, bpmnFactory);
                        commands.push(cmdHelper.addElementsTolist(element, extensionElements, 'values', [newFieldElem]));

                    }
                    if (notification_data.notificationSubject !== '' && notification_data.notificationSubject !== null) {
                        var newFieldElem1 = elementHelper.createElement(CAMUNDA_FIELD_EXTENSION_ELEMENT, {
                            name: 'subject',
                            string: notification_data.notificationSubject
                        }, extensionElements, bpmnFactory);
                        commands.push(cmdHelper.addElementsTolist(element, extensionElements, 'values', [newFieldElem1]));
                    }
                    if (notification_data.notificationCc !== '' && notification_data.notificationCc !== null) {
                        var newFieldElem2 = elementHelper.createElement(CAMUNDA_FIELD_EXTENSION_ELEMENT, {
                            name: 'cc',
                            string: notification_data.notificationCc
                        }, extensionElements, bpmnFactory);
                        commands.push(cmdHelper.addElementsTolist(element, extensionElements, 'values', [newFieldElem2]));
                    }
                    if (notification_msg !== '' && notification_msg !== null) {
                        var newFieldElem3 = elementHelper.createElement(CAMUNDA_FIELD_EXTENSION_ELEMENT, {
                            name: 'text',
                            string: notification_msg
                        }, extensionElements, bpmnFactory);
                        commands.push(cmdHelper.addElementsTolist(element, extensionElements, 'values', [newFieldElem3]));
                    }
                    if (notification_data.notificationBcc !== '' && notification_data.notificationBcc !== null) {
                        var newFieldElem4 = elementHelper.createElement(CAMUNDA_FIELD_EXTENSION_ELEMENT, {
                            name: 'bcc',
                            string: notification_data.notificationBcc
                        }, extensionElements, bpmnFactory);
                        commands.push(cmdHelper.addElementsTolist(element, extensionElements, 'values', [newFieldElem4]));
                    }

                    var roles = [];
                    notification_data.notificationRoles.map(role => {
                        roles.push(role.roleName)
                    });
                    var data = roles.join(",");
                    if (data !== '' && data !== null) {
                        var newFieldElem5 = elementHelper.createElement(CAMUNDA_FIELD_EXTENSION_ELEMENT, {
                            name: 'toRoles',
                            string: data
                        }, extensionElements, bpmnFactory);
                        commands.push(cmdHelper.addElementsTolist(element, extensionElements, 'values', [newFieldElem5]));
                    }

                    if (notification_data.notificationFrom !== '' && notification_data.notificationFrom !== null) {
                        var newFieldElem6 = elementHelper.createElement(CAMUNDA_FIELD_EXTENSION_ELEMENT, {
                            name: 'from',
                            string: notification_data.notificationFrom
                        }, extensionElements, bpmnFactory);
                        commands.push(cmdHelper.addElementsTolist(element, extensionElements, 'values', [newFieldElem6]));
                    }
                    //   commands.push(cmdHelper.updateBusinessObject(newFieldElem, "camunda:string", props));
                },
                async: false
            });
            var selectNotification = values.selectNotification;
            /*  newFieldElem = elementHelper.createElement('camunda:Field', props, extensionElements, bpmnFactory);
              commands.push(cmdHelper.addElementsTolist(element, extensionElements, 'values', [ newFieldElem ]));*/

            //     var field = elementHelper.createElement('camunda:Field', { name: "formId" }, extensionElements, bpmnFactory);
            /*    commands.push(cmdHelper.updateBusinessObject(element, extensionElements, {
                    fields: [ field ]
                  }));*/

            //cmdHelper.updateBusinessObject(element, bo, props);
            //   var  variable = elementHelper.createElement('camunda:String', {}, field, bpmnFactory);

            //   commands.push(cmdHelper.updateBusinessObject(element, field, { 'string': variable }));


            //   if (is(element, 'bpmn:SendTask')) {

            //     }
//             return {selectNotification: values.selectNotification};
            return commands;
        },
        get: function (element, node) {
            var bo = getBusinessObject(element);


            var notificationId = '';
            if (bo.get("extensionElements")) {
                notificationId = bo.get("extensionElements").get("values")[0].get("string");
                //  console.log(bo.get('bpmn:ExtensionElements'))
                // var boResultVariable = bo.get('camunda:resultVariable');

                //  var boResultVariable = bo.get('bpmn:ExtensionElements');
                //     console.log(boResultVariable)

                var selectedNotification = null;
                $.ajax({
                    url: MAIL_NOTIFICATION_SERVICE + "/api/notification/auth/getByNotificationId/" + notificationId,
                    // url: "http://41.230.29.238:8081/ISO-1-0.0.1-SNAPSHOT/api/auth/getRoles",
                    method: "GET",
                    headers: {
                        "X-Tenant": JSON.parse(localStorage.getItem('domain_data')).domainName
                    },
                    success: function (result) {
                        //  var notification_data = JSON.parse(result);
                        console.log(result);
                        selectedNotification = {name: result.name + "-" + result.version, value: result.notificationId}
                    },
                    async: false
                });
            }


            return {selectNotification: notificationId};
            // return { selectNotification:  selectedNotification };
        }
    });
    // implementation type ////////////////////////////////////
    group.entries.push(notificationListSelectEntry);
}
    if(element.type === "bpmn:ServiceTask") {
        //var commands = []
        console.log(element)
        group.entries = group.entries.concat(delegate(element, bpmnFactory, {
            getBusinessObject: getBusinessObject,
            getImplementationType: getImplementationType
        }, translate));

    }
 /* group.entries = group.entries.concat(implementationType(element, bpmnFactory, {
    getBusinessObject: getBusinessObject,
    getImplementationType: getImplementationType,
    hasDmnSupport: hasDmnSupport,
    hasExternalSupport: hasExternalSupport,
    hasServiceTaskLikeSupport: true
  }, translate));


  // delegate (class, expression, delegateExpression) //////////

  group.entries = group.entries.concat(delegate(element, bpmnFactory, {
    getBusinessObject: getBusinessObject,
    getImplementationType: getImplementationType
  }, translate));


  // result variable /////////////////////////////////////////

  group.entries = group.entries.concat(resultVariable(element, bpmnFactory, {
    getBusinessObject: getBusinessObject,
    getImplementationType: getImplementationType,
    hideResultVariable: function(element, node) {
      return getImplementationType(element) !== 'expression';
    }
  }, translate));

  // external //////////////////////////////////////////////////

  if (hasExternalSupport) {
    group.entries = group.entries.concat(external(element, bpmnFactory, {
      getBusinessObject: getBusinessObject,
      getImplementationType: getImplementationType
    }, translate));
  }


  // dmn ////////////////////////////////////////////////////////

  if (hasDmnSupport) {
    group.entries = group.entries.concat(callable(element, bpmnFactory, {
      getCallableType: getImplementationType
    }, translate));
  }


  // connector ////////////////////////////////////////////////

  var isConnector = function(element) {
    return getImplementationType(element) === 'connector';
  };

  group.entries.push(entryFactory.link(translate, {
    id: 'configureConnectorLink',
    buttonLabel: translate('Configure Connector'),
    handleClick: function(element, node, event) {

      var connectorTabEl = getTabNode(node, 'connector');

      if (connectorTabEl) {
        triggerClickEvent(connectorTabEl);
      }

      // suppress actual link click
      return false;
    },
    showLink: function(element, node) {
      var link = domQuery('a', node);
      link.textContent = '';

      domClasses(link).remove('bpp-error-message');

      if (isConnector(element)) {
        var connectorId = InputOutputHelper.getConnector(element).get('connectorId');
        if (connectorId) {
          link.textContent = translate('Configure Connector');
        } else {
          link.innerHTML = '<span class="bpp-icon-warning"></span> ' + escapeHTML(translate('Must configure Connector'));
          domClasses(link).add('bpp-error-message');
        }

        return true;
      }

      return false;
    }
  }));
*/
};



// helpers ///////////////////////////

function getTabNode(el, id) {
  var containerEl = domClosest(el, '.bpp-properties-panel');

  return domQuery('a[data-tab-target="' + id + '"]', containerEl);
}